"use client"

import Link from "next/link"
import { useCart } from "@/contexts/CartContext"
import { useState } from "react"
import Toast from "@/components/Toast"
import { Product } from "@/types/product"

interface ProductViewProps {
  product: Product
}

export default function ProductView({ product }: ProductViewProps) {
  const { addItem } = useCart()
  const [showToast, setShowToast] = useState(false)

  const handleAddToCart = () => {
    addItem({
      id: product.id,
      name: product.name,
      price: product.price,
      image: product.image
    })
    setShowToast(true)
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      {/* Product Navigation */}
      <div className="py-8">
        <Link href="/shop" className="text-gray-600 hover:text-black">
          ‚Üê Back to shop
        </Link>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-x-12 gap-y-8 pb-24">
        {/* Product Image */}
        <div className="aspect-square overflow-hidden rounded-lg bg-gray-100">
          <img
            src={product.image}
            alt={product.name}
            className="h-full w-full object-cover object-center"
          />
        </div>

        {/* Product Info */}
        <div className="flex flex-col">
          <h1 className="text-3xl font-bold">{product.name}</h1>
          <p className="mt-4 text-xl">${product.price.toFixed(2)}</p>
          <p className="mt-4 text-gray-600">{product.description}</p>
          
          <div className="mt-8 prose prose-gray max-w-none">
            <h2 className="text-xl font-semibold mb-4">Details</h2>
            <ul className="space-y-2">
              <li>Free shipping on orders over $50</li>
              <li>30-day return policy</li>
              <li>Handcrafted with care</li>
              <li>Sustainable materials</li>
            </ul>
          </div>

          <button 
            onClick={handleAddToCart}
            className="mt-8 w-full bg-black text-white py-4 px-4 rounded-md hover:bg-gray-800 transition-colors"
          >
            Add to Cart
          </button>
        </div>
      </div>

      {/* Toast Notification */}
      {showToast && (
        <Toast 
          message="Item added to cart"
          onClose={() => setShowToast(false)}
        />
      )}
    </div>
  )
}
